* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: url('./cryptocurrencies-small.png') center/cover no-repeat;
  color: white;
}
@media screen and (min-width: 850px) {
  body {
    background-image: url('./cryptocurrencies.jpg');
  }
}
.error {
  background-color: #b7322c;
  padding: 1rem;
  color: white;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
}
.result {
  color: white;
}
.result h2 {
  font-weight: bold;
  color: white;
}
.result p {
  font-size: 18px!important;
}
.result p span {
  font-weight: bold;
}
p.price{
  font-size: 30px!important;
}